import React,{Component} from 'react'
import axios from "axios"
import {Redirect} from "react-router-dom"
import "./index.scss"
class Conter extends Component{
    constructor(){
        super();
        this.state={
            tokenType:1
        }
    }
    componentWillMount(){
        axios({
            method:"get",
            url:'http://192.168.2.251:7001/center',
            headers:{
				'Authorization':localStorage['token']
			}
        }).then((resp)=>{
            if(resp.data.code===0){
                console.log(resp.data.code);
                this.setState({
                    tokenType:true
                })
                console.log(this.state.tokenType);
            }else{
                this.setState({
                    tokenType:false
                })
                console.log(resp.data.code)
            }
        })       
    }
    logout=()=>{
        // localStorage.removeItem("token")
        this.props.history.push("/")
    }
    exit=()=>{
        localStorage.removeItem("token")
        this.props.history.push("/login")
    }
    componentWillUnmount(){
		this.setState = (state,callback)=>{
			return;
		}
	}
    render(){
        if(this.state.tokenType){
			return <div id="geren">
                <div className="center">
                    <div className="bg-tree"></div>
                    <div className="user-logo"></div>
                    <div className="user-name">莴笋丽</div>
                    <div className="daytime-cloud daytime-cloud1"></div>
                    <div className="daytime-cloud daytime-cloud2"></div>
                    <div className="daytime-cloud daytime-cloud3"></div>
                    <div className="daytime-cloud daytime-cloud4"></div>
                    <div className="u-header">
                        <img data-v-26320141="" src="data:image/png;base64," alt="" className="icon-back" />
                        <img data-v-26320141="" src="data:image/png;base64," alt="" className="btn-cart"></img>
                    </div>
                    <div className="btn-home-wrap" onClick={this.logout}>
                        <img src="data:image/png;base64," className="icon-home" alt="" />
                        回到首页
                        <img src="data:image/png;base64," className="icon-right" alt="" />
                    </div>
                </div>
                <div className="u-order-menu-wrap">
                    <div className="order-menu-title">全部订单
                    <img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACgAAAAWCAMAAACFUC6CAAAACVBMVEXZ2dlMaXHY2NhhhlyoAAAAAnRSTlOAAE0QVXMAAABPSURBVHjabdOxAQAwCMMwh/+P7gEVG0Yr7Z8Yke5U5SSDowyuIIPbIJOTDI4yOMrgvOLAEDJTiJTBUQZHGRxlcJTBUQZHGRxlwUHWgsO7PVBUA5FS+giMAAAAAElFTkSuQmCC" alt="" className="icon-right" />
                    </div>
                    <ul className="order-menu-list">
                        <li className="order-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>代付款</span>
                        </li>
                        <li className="order-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>代发货</span>
                        </li>
                        <li className="order-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>代收货</span>
                        </li>
                        <li className="order-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>代评价</span>
                        </li>
                        <li className="order-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>退货</span>
                        </li>
                    </ul>
                </div>
                <div className="u-common-menu-wrap">
                    <div className="common-menu-title">我猜你经常用</div>
                    <ul className="common-menu-list">
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>消息</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>优惠券</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>我的拼团</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>收货地址</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>联系客服</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>会员卡</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>建议反馈</span>
                        </li>
                        <li className="common-menu" onClick={this.exit}>
                            <img src="data:image/png;base64," alt="" />
                            <span>退出登录</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>关于我们</span>
                        </li>
                        <li className="common-menu">
                            <img src="data:image/png;base64," alt="" />
                            <span>隐私政策</span>
                        </li>
                    </ul>
                </div>
                <div className="u-copyright">
                    <p>版权所有鄂ICP备11005814号-10</p>
                    <p>粤公网安备 44010402000077号</p>
                    <p>广州屈臣氏个人用品商店有限公司</p>
                    <p>地址：广州市越秀区东风东路丽丰中心</p>
                </div>
			</div>
		}else{
            console.log(this.state.tokenType)
			return <Redirect to='/login' />
		}
    }
}
export default Conter;
