import React,{Component} from 'react';
import {Button} from 'antd';
import './index.scss';
class Cart extends Component{
	constructor(){
		super();
		this.state={
			shopType:false,//购物车为空
            shopArr:[],
            zongjia:0,
            num:0
		}
	}
	
	componentDidMount(){
		//判断购物车是否为空
        let arr = JSON.parse(localStorage.getItem('cart'));
        // console.log(arr)
		if(arr != null &&arr.length){
            arr.map((item)=>{ 
                return this.setState({
                    shopType:true,
                    shopArr:arr,
                    allprice:item.num*item.max_app_price
                })
			})
		}
    }
	back=()=>{
		this.props.history.go(-1);
    }
    goHome=()=>{
		this.props.history.push("/");
    }
    jian=(e)=>{
        let arr = JSON.parse(localStorage.getItem('cart'));
        arr[e].num--;
        arr[e].zongjia=arr[e].num*arr[e].max_app_price;
        let allprice = 0;
        arr.map((item,index)=>{
            return eval(allprice= item.zongjia+"+"+allprice)
        })
        // console.log(allprice)
        // console.log(eval(allprice))
        localStorage.setItem('cart',JSON.stringify(arr));
        let Arr = JSON.parse(localStorage.getItem('cart'));
        if(Arr != null &&Arr.length){
            Arr.map((item)=>{ 
                return this.setState({
                    shopType:true,
                    shopArr:Arr,
                    allprice:eval(allprice)
                })
            })
        }
    }
    add=(e)=>{
        let arr = JSON.parse(localStorage.getItem('cart'));
        arr[e].num++;
        arr[e].zongjia=arr[e].num*arr[e].max_app_price;
        let allprice = 0;
        arr.map((item,index)=>{
            return eval(allprice= item.zongjia+"+"+allprice)
        })
        localStorage.setItem('cart',JSON.stringify(arr));
        let Arr = JSON.parse(localStorage.getItem('cart'))
        if(Arr != null &&Arr.length){
            Arr.map((item)=>{ 
                return this.setState({
                    shopType:true,
                    shopArr:Arr,
                    allprice:eval(allprice)
                })
            })
        }
    }
    delete=(e)=>{
        let Arr = JSON.parse(localStorage.getItem('cart'));
        Arr.splice(e,1);
        // console.log(e)
        Arr.map((item)=>{ 
            return this.setState({
                shopArr:Arr
            })
        })      
        localStorage.setItem('cart',JSON.stringify(Arr)); 
        if( Arr.length===0){
            this.setState({
                shopType:false
            })
        } 
    }
	render(){
		return (
			<div className="cart">
                <div className="nav">
                    <span onClick={this.back}>{"<"}</span>
                    <span>购物车</span>
                </div>
				<div className="cart_content">
				{
					this.state.shopType?<ul>
					{
						this.state.shopArr.map((item,index)=><li key={index}>
							<img src={item.over_image_url} alt={item.name}/>
							<span>{item.name}</span>
							<span>{item.max_app_price}</span> 
                            <button onClick={()=>this.jian(index)} disabled={item.num<=1?true:false}>-</button><span>{item.num}</span><button onClick={()=>this.add(index)}>+</button>
                            <span>{item.zongjia}</span><button className="delete" onClick={()=>this.delete(index)}>删除</button>
						</li>)
                    }
                    <p className="shuai">总价:{this.state.allprice}</p>
                    </ul>
                    :<div className ="cart-tishi">
						<div>	
							<img src="data:image/png;base64," alt=""/>
						</div>
						<div>剁剁剁!装满购物车!</div>
						
						<div>
							<Button type="primary" className="cart_btn" onClick={this.goHome}>去首页逛逛</Button>
						</div>
					</div>
				}
				</div>
			</div>
		)
	}
}

export default Cart;